import React, { useContext, useEffect, useState } from 'react';
import { DareChallengerContext } from './FriendLayout'


function FriendAttempterScore() {

    const { params } = useContext(DareChallengerContext);

    const [userName, SetUsername] = useState();
    const [getScored, setGetScroed] = useState([]);

    const fetchData = async () => {
        const response = await fetch(`http://159.89.168.28/getAllChallengerByQuizId/${params.challengeId}`)
        if (!response.ok) {
            throw new Error('Data could not be fetched!')
        } else {
            return response.json()
        }
    }

    useEffect(() => {
        fetchData()
            .then((res) => {
                setGetScroed(res.quiz_performer)
                SetUsername(res.quiz_owner)
            })
            .catch()
    }, []);


    return (
        <div className="container">
            <div className="col-md-6 offset-md-3">
                <div className="card mt-5">
                    <div className="card-body p-4 shadow text-center">
                        <img src="https://dares.in/en/upload/pus.png?v=88" className="max-width-100" alt="challenge header img" />
                        <h4>Friendship Dare {new Date().getFullYear()}</h4>
                        <div className="mt-5 text-center">
                            <h5 className='my-2'>Who Knows {userName} Best?</h5>
                            <table className="table table-bordered table-striped">
                                <thead >
                                    <tr>
                                        <th className="bg-dark-subtle">Name</th>
                                        <th className="bg-dark-subtle">Score</th>
                                    </tr>
                                </thead>
                                <tbody>
                                    {getScored && getScored?.map((x, i) => {
                                        return (<tr key={i}>
                                            <td className='text-capitalize'>
                                                {x.c_name}
                                            </td>
                                            <td>
                                                {x.c_score}
                                            </td>
                                        </tr>)
                                    })}
                                </tbody>
                            </table>
                        </div>
                        <a href={window.location.origin} onClick={()=>{localStorage.clear()}}  className="btn btn-success mt-2">Create Your Own Quiz</a>
                    </div>
                </div>
            </div>
        </div>
    );
}

export default FriendAttempterScore;