
import { useState, useContext, useEffect } from 'react';
import { DareAdminContext } from './AdminLayout';

function SiteSettings() {
    const { token, handleAdminClick } = useContext(DareAdminContext);
    const [siteData, setSiteData] = useState({});
    const [imgdata, setimgdata] = useState({});
    const FetchToken = JSON.parse(token);

    const fetchSiteData = async () => {
        const response = await (await fetch(`http://159.89.168.28/getsite`, {
            method: 'GET',
            headers: {
                'Content-Type': 'application/json',
                "Authorization": FetchToken.accessToken
            }
        }))
        if (!response.ok) {
            throw new Error('Data coud not be fetched!')
        } else {
            return response.json()
        }
    }

    useEffect(() => {
        fetchSiteData().then(e => {
            console.log(e)
            setSiteData(e[0])
        })
    }, [])


    const handleChange = (event) => {
        const name = event.target.name;
        const value = event.target.value;
        setSiteData(values => ({ ...values, [name]: value }))
    }
    const handleImgChange = (event) => {
        setimgdata(imgdata => ({ ...imgdata, "img": event.target.files[0], "site_og_images": URL.createObjectURL(event.target.files[0]) }))
    }

    const formdata = new FormData();
    const handleSubmit = (event) => {
        event.preventDefault();
        if (imgdata.img) {
            formdata.append("img", imgdata.img);
        }
        formdata.append("SiteData", JSON.stringify(siteData));

        updateSiteData().then((e)=>{
            if(e?.res?.status === 200){
                handleAdminClick({"siteDaataUpdate": true});
            }
            console.log(e)}).catch((e)=>console.log(e))
    }
    const updateSiteData = async () => {
        const response = await (await fetch(`http://159.89.168.28/updateSite`, {
            method: 'POST',
            headers: {
                // 'Content-Type': 'application/json',
                "Authorization": FetchToken.accessToken
            },
            body: formdata
        }))
        if (!response.ok) {
            throw new Error('Data coud not be fetched!')
        } else {
            return response.json()
        }
    }
    return (
        <div className="container">
            <div className="row">
                <div className="col-md-6">
                    <h1 className="mt-5">Site Settings</h1>
                    <p className="lead">
                        <form method="post" onSubmit={handleSubmit} className="form-horizontal" >

                            <div className="form-group">
                                <label htmlFor="site_title">Site Title</label>
                                <input type="text" className="form-control" name="site_title" id="site_title" placeholder="Enter site title" onChange={handleChange} value={siteData?.site_title} />
                            </div>

                            <div className="form-group">
                                <label htmlFor="site_short_title">Site Short Title</label>
                                <input type="text" className="form-control" name="site_short_title" id="site_short_title" placeholder="Enter short title" onChange={handleChange} value={siteData?.site_short_title} />
                            </div>

                            <div className="form-group">
                                <label htmlFor="site_wishing_web">Wishing Website Url</label>
                                <input type="text" className="form-control" name="site_wishing_web" id="site_wishing_web" placeholder="Enter using website url" onChange={handleChange} value={siteData?.site_wishing_web} />
                            </div>

                            <div className="form-group">
                                <label htmlFor="site_description">Meta Description</label>
                                <textarea className="form-control" name="site_description" id="site_description" rows="5" required="" spellCheck="true" onChange={handleChange} value={siteData?.site_description}>
                                </textarea>
                            </div>

                            <div className="form-group">
                                <label htmlFor="site_og_image">Select Og image</label>
                                <input type="file" className="form-control" name="site_og_image" placeholder="select image" onChange={handleImgChange} />
                                <span className="text-danger"> OG image must be 1200X1200 pixel in size</span>
                            </div>

                            <button type="submit" className="btn btn-success float-right">Update</button>
                        </form>
                    </p>
                </div>
                <div className="col-md-4">
                    {/* <img className="img-thumbnail img-fluid mt-5" src={`http://159.89.168.28/${inputs.site_og_image}`} alt='sdfsd' /> */}
                    <img className="img-thumbnail img-fluid mt-5" src={`http://159.89.168.28/public/${siteData?.site_og_images}`} alt='sdfsd' />
                </div>
            </div>
        </div>
    );
}

export default SiteSettings;