
import { useContext } from 'react';
import besti from '../../Assets/besti.png';
import dare from '../../Assets/dare.png';
import twentytwo from '../../Assets/2022.png';
import truth from '../../Assets/truth.png';
import { DareClientContext } from './LayoutClient'
function Challengecard() {

    const { handleUserClick } = useContext(DareClientContext);

    const handleChallengeClick = event => {
        handleUserClick({ "challengedSelected": true })
    };
    return (<>
        <div className="container">
            <div className="offset-md-2 col-md-8">

                <div className="dare_card_container box">
                    <img src={besti} alt="Besti" className="max-width-200"  />
                    <h3 className="">
                        Friendship Dare {new Date().getFullYear()}
                    </h3>
                    <button className="bg-danger border-radius-0 text-white" value={1} onClick={handleChallengeClick}>
                        Start
                    </button>
                </div>

                <div className="dare_card_container box">
                    <img src={dare} alt="Besti" className="max-width-200"  />
                    <h3 className="">Friendship Meter</h3>

                    <button className="bg-danger border-radius-0 text-white " value={2} onClick={handleChallengeClick}>
                        Start
                    </button>
                </div>

                <div className="dare_card_container box">
                    <img src={twentytwo} alt="Besti" className="max-width-200"  />
                    <h3 className="">
                        Friends Dare Challenge {new Date().getFullYear()}
                    </h3>

                    <button className="bg-danger border-radius-0 text-white" value={3} onClick={handleChallengeClick}>
                        Start
                    </button>

                </div>
                <div className="dare_card_container box">
                    <img src={truth} alt="Besti" className="max-width-200"  />

                    <h3 className="">
                        TRUTH or FALSE
                    </h3>

                    <button className="bg-danger border-radius-0 text-white" value={4} onClick={handleChallengeClick}>
                        Start
                    </button>

                </div>
            </div>
        </div>
    </>
    );
}

export default Challengecard;