import React, { useContext, useCallback, useEffect, useState } from 'react';
import { DareChallengerContext } from './FriendLayout'
import { CopyToClipboard } from 'react-copy-to-clipboard';
import { useNavigate } from 'react-router-dom';
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';

function FreindChallengeScoreCard() {
    const navigate = useNavigate();
    const { params } = useContext(DareChallengerContext);
    const [userName, SetUsername] = useState();
    const [copied, setCopied] = useState(false);
    const [locations, setlocation] = useState("");
    const [getScored, setGetScroed] = useState([]);
    const [AdURL, SetAdURL] = useState("");

    const onCopy = useCallback(() => {
        setCopied(true);
        toast("Copied")
    }, [])
    const DeleteUserData = () => {
        localStorage.clear();
        navigate('/')
    }

    const fetchData = async () => {

        const response = await fetch(`http://159.89.168.28/getAllChallengerByQuizId/${params.challengeId}`)
        if (!response.ok) {
            throw new Error('Data could not be fetched!')
        } else {
            return response.json()
        }
    }

    useEffect(() => {
        fetchData()
            .then((res) => {
                setGetScroed(res)
                SetUsername(res.quiz_performer)
                setlocation(`${window.location.origin + '/' + params.challengeId}`)
                SetAdURL(res.advertisementURL)
            })
            .catch()
    }, []);
    
    return (
        <div className="container">
            <div className="col-md-6 offset-md-3">
                <div className="card mt-5">
                    <div className="card-body p-4 shadow text-center">
                        <img src="https://dares.in/en/upload/pus.png?v=88" className="max-width-100" alt="challenge header img" />
                        <h4>Your Challenge is Ready</h4>
                        <p>Share this link with your friends</p>
                        <div className="d-flex">
                            <input className="border btn shadow w-100 border-radius-0 " type="input" readOnly value={locations} />
                            <CopyToClipboard onCopy={onCopy} text={locations}>
                                <button className="btn btn-success input-group-text border-radius-0">Copy This Link</button>
                            </CopyToClipboard>
                        </div>

                        <div className="col-md-12 mt-4">
                            <a className="btn btn-success mb-2 w-100" href={`https://wa.me/?text=${locations}`} target="_blank">
                                <img src="https://quiz.quotedtale.com/assets/images/wa17.png" alt="whatsapp img" /> <span className="ms-1">Send to WhatsApp </span>
                            </a>
                        </div>


                        <div className="mt-3">
                            👇👇👇👇👇👇👇👇👇👇👇👇<br />
                            👉<a href={AdURL} target="_blank">
                                Touch this blue line and see magic
                            </a>👈
                            <br />☝☝☝☝☝☝☝☝☝☝☝☝
                        </div>
                        <div className="mt-3">
                            <button className='btn btn-link' onClick={DeleteUserData}>You can delete this quiz by clicking here</button>
                        </div>

                    </div>
                </div>


                <div className="mt-5 text-center">


                    <h5>Who Knows you Best?</h5>
                    <table className="table table-bordered table-striped">
                        <thead >
                            <tr>
                                <th className="bg-dark-subtle">Name</th>
                                <th className="bg-dark-subtle">Score</th>
                            </tr>
                        </thead>
                        <tbody>

                            {getScored &&  getScored?.quiz_performer?.map((x, i) => {
                                return (<tr key={i} >
                                    <td className='text-capitalize'>
                                        {x.c_name}
                                    </td>
                                    <td>
                                        {x.c_score}
                                    </td>
                                </tr>)

                            })}
                        </tbody>
                    </table>
                    <ToastContainer />

                </div>
            </div>
        </div>

    );
}

export default FreindChallengeScoreCard;