
import React from 'react';
import { Route, Routes } from 'react-router-dom';
import './App.css';
import AdminLayout from './components/Admin/AdminLayout';
import NoPage from './components/NoPage';
import LayoutClient from './components/Client/LayoutClient';
import FriendLayout from './components/FriendChallenges/FriendLayout';
import { useState, useEffect } from 'react';
import { Helmet } from 'react-helmet';
function App() {
  const [inputs, setInputs] = useState({});
  const fetchSiteData = async () => {
    const response = await (await fetch(`http://159.89.168.28/getsite`, {
      method: 'GET',
      headers: {
        'Content-Type': 'application/json',
      }
    }))
    if (!response.ok) {
      throw new Error('Data coud not be fetched!')
    } else {
      return response.json()
    }
  }

  useEffect(() => {
    fetchSiteData().then(e => {
      setInputs(e[0])
      localStorage.setItem("siteData", JSON.stringify(e[0]))
    })

  }, [])
  return (<div>
    {/* <Helmet>
      <title>{inputs?.site_title}</title>
      <link
      rel="icon" sizes="16x16"
      type="image/png" // Adjust the image type if needed (e.g., image/x-icon for .ico files)
      href="https://dares.in/en/upload/pus.png?v=88"
    /> 
    
      <meta name="site_description" content={inputs?.site_description} />
      <meta property="og:image" content={process.env.REACT_APP_BASE_URL+'/'+inputs?.site_og_image}></meta>
    </Helmet> */}
    <Routes>
      <Route path="/" element={<LayoutClient />} />
      <Route path="/:challengeId" element={<FriendLayout />} />
      <Route path="admin/*" element={<AdminLayout />} />
      <Route path="*" element={<NoPage />} />
    </Routes>
  </div>
  );
}

export default App;
