import React, { useContext, useRef } from 'react';
import { DareClientContext } from './LayoutClient'
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
function StartCardData() {
    const inputhandleUserNameRef = useRef(null);
    const { handleUserClick } = useContext(DareClientContext);
    const handleStartCardEventFn = (e) => {
        if (!!inputhandleUserNameRef.current.value) {
            handleUserClick({ "userName": inputhandleUserNameRef.current.value })
        }
        else {
            toast("User Name is required")
        }
    };
    return (
        <div className="container">
            <div className="col-md-6 offset-md-3">
                <div className="card mt-5 border-1-black">
                    <div className="card-body text-center">
                        <img src="https://dares.in/en/upload/pus.png?v=88" className="max-width-100" alt="challenge header img" />
                        <h2>Do Your Friends Know You?</h2>
                        <div className="input-group mb-3">
                            <span className="input-group-text"><img src="https://dares.in/en/assets/images/user.png" alt="" /></span>
                            <input maxLength={20} ref={inputhandleUserNameRef} type="text" className="form-control" placeholder="Username" />
                        </div>
                        <div>
                            <button className="btn btn-success" onClick={handleStartCardEventFn}>👉Start👈</button>
                        </div>
                    </div>
                </div>
            </div>
            <ToastContainer />
        </div>
    );
}

export default StartCardData;