import React, { useContext, useRef, useEffect, useState } from 'react';
import { DareChallengerContext } from './FriendLayout'
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
function FriendChallengeName() {

    const inputhandleUserNameRef = useRef(null);
    const { params, handleUserClick } = useContext(DareChallengerContext);
    const [userName, SetUsername] = useState();
    const [getScored, setGetScroed] = useState([]);
    const handleStartCardEventFn = (e) => {
        if (!!inputhandleUserNameRef.current.value) {
            handleUserClick({ "userNameChallenger": inputhandleUserNameRef.current.value })
            localStorage.setItem("userNameChallenger", inputhandleUserNameRef.current.value)
        }
        else {
            toast("User Name is required")
        }
    };
    const fetchData = async () => {
        const response = await fetch(`http://159.89.168.28/getAllChallengerByQuizId/${params.challengeId}`)
        if (!response.ok) {
            throw new Error('Data could not be fetched!')
        } else {
            return response.json()
        }
    }

    useEffect(() => {
        fetchData()
            .then((res) => {
                setGetScroed(res.quiz_performer)
                SetUsername(res.quiz_owner)
            })
            .catch((e) => {
                console.log(e.message)
            })
    }, []);


    return (
        <div className="container">
            <div className=" col-md-6  offset-md-3">
                <div className="card mt-5">
                    <div className="bg-light card-body p-5 shadow text-center">
                        <img src="https://dares.in/en/upload/pus.png?v=88" className="max-width-100" alt="challenge header img" />
                        <h4>Friendship Dare {new Date().getFullYear()}</h4>
                        <p>How Well You Really Know {userName}</p>
                        <div className="input-group mb-3">
                            <span className="input-group-text"><img src="https://dares.in/en/assets/images/user.png" alt="" /></span>
                            <input minLength={3} maxLength={20} ref={inputhandleUserNameRef} type="text" className="form-control" placeholder="Enter your full name" />
                        </div>
                        <div>
                            <button className="btn btn-success" onClick={handleStartCardEventFn}>Start</button>
                        </div>
                        <div className='my-3'>
                            <h5>Who Knows {userName} Best?</h5>
                            <div className="text-center">
                                <table className="table table-bordered table-striped">
                                    <thead >
                                        <tr>
                                            <th className="bg-dark-subtle">Name</th>
                                            <th className="bg-dark-subtle">Score</th>
                                        </tr>
                                    </thead>
                                    <tbody>
                                        {getScored && getScored?.map((x, i) => {
                                            return (<tr key={i}>
                                                <td className='text-capitalize'>
                                                    {x.c_name}
                                                </td>
                                                <td>
                                                    {x.c_score}
                                                </td>
                                            </tr>)

                                        })}
                                    </tbody>
                                </table>
                            </div>
                        </div>

                        <a href={window.location.origin} onClick={()=>{localStorage.clear()}}  className="btn btn-success mt-2">Create Your Own Quiz</a>
                    </div>
                </div>
            </div>
            <ToastContainer />
        </div>

    );
}
export default FriendChallengeName;